.wrap_movie_detail{
	width: 1100px;
	margin: 0 auto;
	padding: 100px;
}
.cont_movie_detail{
	padding: 50px;
	border-radius: 16px;
	background: #1C1E21;
}
.info_movie{
	overflow: hidden;
}
.info_movie .movie_thumb{
	float: left;
	overflow: hidden;
	width: 200px;
	height: 280px;
	margin-right: 50px;
	border-radius: 10px;
}
.info_movie .img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.movie_contents{
	overflow: hidden;
}
.movie_contents h1{
	padding-bottom: 20px;
	font-size: 25px;
}
.movie_contents .list_info .tit_info{
	float: left;
	width: 50px;
	color: #bbb;
}
.movie_contents .list_info .txt_info{
	overflow: hidden;
}
.movie_contents .list_info .txt_g{
	position: relative;
}
.movie_contents .list_info .txt_g + .txt_g:before{
	content: '';
	display: inline-block;
	width: 1px;
	height: 14px;
	margin: 6px 13px;
	vertical-align: top;
	background-color: rgba(255,255,255,0.2);
}
.info_credit{
	padding-top: 80px;
	margin: 0 -5px;
}
.list_credit{
	overflow: hidden;
}
.list_credit li{
	float: left;
	width: 100px;
	margin: 5px 5px 0;
}
.info_credit .tit_credit{
	display: block;
	padding-bottom: 20px;
	font-weight: 400;
	font-size: 22px;
}
.list_credit li img{
	display: block;
	width: 100px;
	height: 150px;
	object-fit: cover;
	border-radius: 2px;
}
.list_credit .tit_cast{
	display: block;
	overflow: hidden;
	padding: 10px 0;
	font-size: 15px;
	white-space: nowrap;
	text-overflow: ellipsis;
	color: rgba(255,255,255,0.7);
}