.wrap_movie{
	width: 1120px;
	margin: 0 auto;
	padding: 0 50px 100px;
}
.cont_movie h1{
	padding: 50px 0;
	font-size: 40px;
}
.list_movie{
	overflow: hidden;
	margin: -70px -15px 0;
}
.list_movie li{
	float: left;
	width: 200px;
	margin: 70px 15px 0;
}
.list_movie li:nth-child(5n+1){
	clear: both;
}