.item_movie .movie_thumb{
	display: block;
	overflow: hidden;
	width: 200px;
	height: 280px;
	border-radius: 10px;
}
.item_movie img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.item_movie .link_detail{
	display: block;
	overflow: hidden;
	padding: 10px;
	font-size: 18px;
	color: #fff;
	text-align: center;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.item_movie .desc_overview{
	display: -webkit-box;
	overflow: hidden;
	max-height: 100px;
	line-height: 20px;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 5;
	color: rgba(255,255,255,0.6);
}